html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-planet-00 {
  width: 1.01em;
  height: 1em;
  fill: #69899e;
}

.icon-planet-01 {
  width: 0.98em;
  height: 1em;
  fill: #e5a248;
}

.icon-planet-02 {
  width: 0.91em;
  height: 1em;
  fill: #b29a76;
}

.icon-planet-03 {
  width: 1.01em;
  height: 1em;
  fill: #9abdcc;
}

@keyframes spinsun {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(-360deg);
  }
}

@keyframes shadow {
  0% {
    background-position: 130% 0%;
  }
  33% {
    background-position: 50% 0%;
  }
  55% {
    background-position: 0% 0%;
  }
  80% {
    background-position: -50% 0%;
  }
  100% {
    background-position: -50% 0%;
  }
}

@keyframes orbitearth {
  0% {
    z-index: 4;
    transform: rotateY(0);
  }
  49% {
    z-index: 4;
  }
  50% {
    z-index: -4;
  }
  99% {
    z-index: -4;
  }
  100% {
    z-index: 4;
    transform: rotateY(360deg);
  }
}

@keyframes orbitmars {
  0% {
    z-index: 5;
    transform: rotateY(0);
  }
  49% {
    z-index: 5;
  }
  50% {
    z-index: -5;
  }
  99% {
    z-index: -5;
  }
  100% {
    z-index: 5;
    transform: rotateY(360deg);
  }
}

@keyframes orbitjupiter {
  0% {
    z-index: 6;
    transform: rotateY(270);
  }
  49% {
    z-index: 6;
  }
  50% {
    z-index: -6;
  }
  99% {
    z-index: -6;
  }
  100% {
    z-index: 6;
    transform: rotateY(360deg);
  }
}

@keyframes orbitsaturn {
  0% {
    z-index: 7;
    transform: rotateY(270);
  }
  49% {
    z-index: 7;
  }
  50% {
    z-index: -7;
  }
  99% {
    z-index: -7;
  }
  100% {
    z-index: 7;
    transform: rotateY(360deg);
  }
}

/* Keep planet image flat */
@keyframes anti-spin {
  from {
    transform: rotateY(0);
  }
  to {
    transform: rotateY(-360deg);
  }
}

@keyframes anti-spin-rings {
  from {
    transform: rotateY(0) rotateX(73deg);
  }
  to {
    transform: rotateY(-360deg) rotateX(73deg);
  }
}

/* scene wrapper */
body {
  height: 100%;
}

.out {
  overflow: hidden;
  height: 100vh;
}

.wrapper {
  position: relative;
  margin: 0 auto;
  display: inline-block;
  transform: rotate(-10deg) translate3d(0, 50%, 0);
}

.wrapper > div {
  position: relative;
  margin: 0 auto;
  transform-style: preserve-3d;
  height: 0px;
}

.sun {
  width: 60px;
  position: absolute;
  top: 0px;
  z-index: 1;
  height: 60px !important;
}

.sun .star {
  width: 60px;
  height: 60px;
  background: url("../img/sun.svg") no-repeat;
  background-size: cover;
  border-radius: 250px;
  margin: 0 auto;
  animation: spinsun 40s infinite linear;
}

.planet {
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  animation-iteration-count: infinite;
  overflow: hidden;
}

.shadow {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  background: transparent url(http://www.waynedunkley.com/img/solar_system/shadow.png) 0% 0% no-repeat;
  background-size: cover;
  border-radius: 100%;
}

.earth {
  position: absolute;
  width: 610px;
  z-index: 4;
  animation: orbitearth 20s infinite linear;
  top: -20px;
}

.earth .planet {
  width: 40px;
  height: 40px;
  background-image: url("../img/earth.svg");
  background-size: cover;
  animation: anti-spin 20s infinite linear;
}

.earth .shadow {
  animation: shadow 20s infinite linear;
}

.mars {
  position: absolute;
  width: 706px;
  z-index: 5;
  animation: orbitmars 30s infinite linear;
  top: -11px;
}

.mars .planet {
  width: 22px;
  height: 22px;
  background-image: url("../img/mars.svg");
  animation: anti-spin 30s infinite linear;
}

.mars .shadow {
  animation: shadow 30s infinite linear;
}

.jupiter {
  position: absolute;
  width: 100%;
  max-width: 1100px;
  z-index: 6;
  animation: orbitjupiter 50s infinite linear;
  top: -64px;
}

.jupiter .planet {
  width: 90px;
  height: 90px;
  background: url("../img/jupiter.svg") no-repeat;
  background-size: cover;
  animation: anti-spin 50s infinite linear;
}

.jupiter .shadow {
  animation: shadow 50s infinite linear;
}

.icon-planet-00 {
  width: 100px;
  height: 100px;
}
